function accordion() {
	document.querySelectorAll(".accordion").forEach(accordion => {
		const wrapper = accordion.querySelector(".accordion__wrapper")
		const toggler = accordion.querySelector(".accordion__toggler")
		const content = accordion.querySelector(".accordion__content")

		toggler.addEventListener("click", () => {
			wrapper?.classList.toggle("is-active")
			toggler?.classList.toggle("is-active")
			content?.classList.toggle("is-active")
			accordion.classList.toggle("is-active")
		})
	})

	document.querySelectorAll("[data-accordion]").forEach(accordion => {
		const wrapper = accordion.querySelector("[data-accordion-wrapper]")
		const toggler = accordion.querySelector("[data-accordion-toggler]")
		const content = accordion.querySelector("[data-accordion-content]")

		// accordion.removeAttribute("data-accordion")
		// wrapper.removeAttribute("data-accordion-wrapper")
		// toggler.removeAttribute("data-accordion-toggler")
		// content.removeAttribute("data-accordion-content")

		function expand() {
			wrapper?.classList.toggle("is-active", true)
			toggler?.classList.toggle("is-active", true)
			content?.classList.toggle("is-active", true)
			accordion.classList.toggle("is-active", true)
		}

		function collapse() {
			wrapper?.classList.toggle("is-active", false)
			toggler?.classList.toggle("is-active", false)
			content?.classList.toggle("is-active", false)
			accordion.classList.toggle("is-active", false)
		}

		function toggle(force) {
			wrapper?.classList.toggle("is-active", force)
			toggler?.classList.toggle("is-active", force)
			content?.classList.toggle("is-active", force)
			accordion.classList.toggle("is-active", force)
		}

		toggler?.addEventListener("click", () => {
			toggle()
		})

		accordions.set(accordion, { expand, collapse, toggle })
	})
}

function accordionList() {
	document.querySelectorAll(".accordion-list").forEach(accordionList => {
		const slaves = accordionList.querySelectorAll("[data-accordion]")

		accordionList.querySelector(".accordion-list__toggler")?.addEventListener("click", () => {
			const hasOpened = accordionList.matches(":has([data-accordion].is-active)")

			slaves.forEach(slave => {
				accordions.get(slave).toggle(!hasOpened)
			})
		})
	})
}

function book() {
	document.querySelectorAll(".book").forEach(book => {
		book.querySelectorAll(".book__tabs").forEach(tabs => {
			const navs = tabs.querySelectorAll(".book__tabs__tab")
			const contents = tabs.querySelectorAll(".book__tabs__content")

			navs.forEach((nav, index) => {
				nav.addEventListener("click", () => {
					navs.forEach((nav, jndex) => {
						nav.classList.toggle("is-active", index == jndex)
						contents.item(jndex)?.classList.toggle("is-active", index == jndex)
					})
				})
			})
		})

		book.querySelectorAll(".book__biblio-card").forEach(card => {
			const button = card.querySelector(".book__biblio-card__button")
			const text = card.querySelector(".book__biblio-card__text")

			button?.addEventListener("click", () => {
				if (navigator.clipboard) {
					try {
						navigator.clipboard.writeText(text.textContent)
							.then(() => {
								tooltip.show(button, "Успешно скопировано")
								setTimeout(() => {
									tooltip.hide()
								}, 1000)
							})
					} catch (error) {
						console.error(error)
						tooltip.show(button, "Произошла ошибка")
						setTimeout(() => {
							tooltip.hide()
						}, 1000)
					}
				} else {
					tooltip.show(button, "Скопируйте выделенный текст")
					setTimeout(() => {
						tooltip.hide()
					}, 1000)
					window.getSelection().selectAllChildren(text)
				}
			})
		})
	})
}

function m31() {
	// TODO корректировка на высоту экрана
	const observer = new IntersectionObserver(entries => {
		entries.forEach(entry => {
			entry.target.classList.toggle("is-visible", entry.isIntersecting)
			entry.target.isVisible = entry.isIntersecting
		})
	})

	const m31s = document.querySelectorAll(".m31")

	m31s.forEach(m31 => {
		m31.style.setProperty("--offset", m31.offsetTop)
		m31.style.setProperty("--scrolled", window.scrollY)
		observer.observe(m31)

		setTimeout(() => {
			m31.classList.add("is-initialized")
		})
	})

	let current
	window.addEventListener("scroll", () => {
		cancelAnimationFrame(current)
		current = requestAnimationFrame(() => {
			m31s.forEach(m31 => {
				if (m31.isVisible) {
					m31.style.setProperty("--scrolled", window.scrollY)
				}
			})
		})
	})
}

function showcase() {
	document.querySelectorAll(".showcase").forEach(showcase => {
		const tabs = showcase.querySelectorAll(".showcase__tab")
		/** 
		* @type {[{item: HTMLElement, itemMain: HTMLElement, itemExtra: HTMLElement, tab: HTMLElement, tabExtra: HTMLElement }]}
		*/
		const items = []

		showcase.querySelectorAll(".showcase__item").forEach((item, index) => {
			items.push({
				item,
				itemMain: item.querySelector(".showcase__item__main"),
				itemExtra: item.querySelector(".showcase__item__extra"),
				tab: tabs.item(index),
				tabMain: tabs.item(index)?.querySelector(".showcase__tab__main"),
				tabExtra: tabs.item(index)?.querySelector(".showcase__tab__extra"),
			})
		})

		items.forEach(item => {
			item.tab?.addEventListener("focusin", () => {
				item.tab.scrollIntoViewIfNeeded()
			})
			item.tab?.addEventListener("click", event => {
				const switchMainExtra = item.itemMain.classList.contains("is-active")

				items.forEach(itemTest => {
					itemTest.item.classList.toggle("is-active", item == itemTest)
					itemTest.tab.classList.toggle("is-active", item == itemTest)
					itemTest.itemMain.classList.toggle("is-active", false)
					itemTest.tabMain.toggleAttribute("disabled", false)
					itemTest.itemExtra?.classList.toggle("is-active", false)
					itemTest.tabExtra?.classList.toggle("is-active", false)
				})

				if (ifClickInside(event, item.tabExtra)) {
					item.itemExtra?.classList.toggle("is-active", switchMainExtra)
					item.tabExtra?.classList.toggle("is-active", switchMainExtra)
					item.itemMain.classList.toggle("is-active", !switchMainExtra)
					item.tabMain.toggleAttribute("disabled", !switchMainExtra)
				} else {
					item.itemExtra?.classList.toggle("is-active", false)
					item.tabExtra?.classList.toggle("is-active", false)
					item.itemMain.classList.toggle("is-active", true)
					item.tabMain.toggleAttribute("disabled", true)
				}
			})
		})
	})
}

